import React from "react";
import {Link} from "react-router-dom";
import PropTypes from "prop-types";

function Movies({poster, title, year, summary, genres, id, yt_trailer_code}){
    return (
        <div className = "movie">
            <Link to = {{
                pathname: `/movie/${id}`,
                state: {poster, title, year, summary, genres, id, yt_trailer_code}
            }}>
                <img className = "movie__img" src = {poster} alt = {title} title = {title}></img>            
                <div className = "movie__data">
                    <h4 className = "movie__title">{title}</h4>
                    <h5 className = "movie__year">{year}</h5>
                    <ul className = "movie__genres">
                        {genres.map((genre, index) => {
                        return <li className = "genres__genre" key = {index}>{genre}</li>
                    })}</ul>
                    <p className = "movie__summary">{summary.slice(0, 200) + "..."}</p>
                </div>
            </Link>
        </div>
    );
}

Movies.propTypes = {
    poster: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    year: PropTypes.number.isRequired
}

export default Movies;