import React from "react";
import "./Detail.css";

class Detail extends React.Component{
    componentDidMount(){
        const {location, history} = this.props;
        console.log(location.state);
        if(location.state === undefined){
            history.push("/");
        }
    }
    render(){
        const {location} = this.props;
        if(location.state){
            return(
                <div className = "detail">
                    <div className = "detail_image">
                        <img src = {location.state.poster} alt = {location.state.title}></img>
                    </div>
                    <div className = "detail__data">
                        <h3 className = "detail__title">{location.state.title}</h3>
                        <h5 className = "detail__year">{location.state.year}</h5>
                        <p className = "detail__summary">{location.state.summary}</p>                    
                    </div>
                    <iframe width="720" height="480" src={"https://www.youtube.com/embed/" + location.state.yt_trailer_code} title = {location.state.id}></iframe>
                </div>
            ); 
        }
        else{
            return null;
        }
    }
}

export default Detail;